import React from "react";

const ServiceList = () => {

  const services = [
    { name: "", href: "" },
    { name: "", href: "" },
    { name: " ", href: "" },
    { name: " ", href: "" },
    { name: " ", href: "" },
  ];

  return (
    <ul className="service_list">
      {services.map((service, index) => (
        <li className="service_item" key={index}>
          <a href={service.href}>{service.name}</a>
        </li>
      ))}
    </ul>
  );
};

export default ServiceList;
